 .home-main {
   display: flex;
   overflow: hidden;
   position: relative;
   justify-content: space-between;
   align-items: center;
 }

 .home-heading {
   position: relative;
   left: 10px;
   z-index: 300;
   width: 600px;
 }

 .scaner {
   width: 150px;
   height: auto;
 }

 .scaner-div {
   display: inline-flex;
   width: 320px;
   gap: 20px;
   margin-top: 50px;
 }

 .home-pic {
   position: absolute;
   right: 1px;
   top: 10%;
   width: 570px;
 }

 .home-icons-div {
   display: flex;
   gap: 50px;
   margin-top: 50px;
 }

 .home-scroller-main {
   display: flex;
   justify-content: center;
   margin: 100px 0px;
   padding-left: 70px;
   padding-right: 70px;
 }

 .trusted-card {
   position: relative;
   border-radius: 10px;
   overflow: hidden;
   transition: box-shadow 0.3s ease;
   height: 490px;
   align-items: center;
   padding: 10px 0px;
   background-color: #D2B805;
 }

 .trusted-card::before {
   content: '';
   position: absolute;
   bottom: 0;
   right: 0;
   width: 0;
   height: 0;
   background: rgba(0, 0, 0, 0.1);
   transition: width 0.3s ease, height 0.3s ease;
   z-index: 0;
 }

 .trusted-card:hover::before {
   width: 100%;
   height: 100%;
 }

 .card-title-in-home {
   text-align: center;
   font-size: 34px;
   text-shadow: 1px 2px 2px gainsboro;
 }

 .trusted-card .card-body {
   position: relative;
   z-index: 1;
 }

 .cards-img-in-home {
   width: 100%;
   height: 300px;
   border-radius: 8px;
 }

 @media screen and (min-width:320px) and (max-width:1170px) {




   .home-main {
     display: flex;
     overflow: hidden;
     position: static;
     min-height: 120vh;
     flex-direction: column;
     flex-direction: column-reverse;
   }

   .home-heading {
     position: static;
     left: 10px;
     z-index: 0px;
     width: 100%;
   }

   .trusted-card {
     margin: 1rem;
   }

   .scaner {
     width: 250px;
     height: auto;
   }

   .scaner-div {
     display: inline-flex;
     width: 100%;
     justify-content: center;
     align-items: center;
     gap: 20px;
     margin-top: 50px;
   }

   .home-pic {
     position: static;
     width: 100%;
   }

   .home-icons-div {
     display: flex;
     gap: 50px;
     margin-top: 50px;
     justify-content: center;
   }

   .grid-2 {
     order: 2;
   }

   .grid-2 {
     order: 1;
   }

 }


